import { Container } from "react-bootstrap";
import { Link } from "react-router-dom";
import cx from "classnames";
import IconTwitter from "assets/images/common/twitter.svg";
import IconTelegram from "assets/images/common/telegram.svg";
import IconDiscord from "assets/images/common/discord.svg";
import IconInstagram from "assets/images/common/instagram.svg";
import IconYoutube from "assets/images/common/youtube.svg";
import IconTiktok from "assets/images/common/tiktok.svg";
import "./index.scss";

const socialLinks = [
  {
    id: "twitter",
    icon: IconTwitter,
    to: "https://x.com/huncoinco?t=VezBX7xGR50ybpKhAWgBSA&s=09",
  },
  {
    id: "telegram",
    icon: IconTelegram,
    to: "https://t.me/+BdmM96UBE8JmMGMy",
  },
  {
    id: "tiktok",
    icon: IconTiktok,
    to: "https://www.tiktok.com/@huncoin.co?_t=8kdhOpEqKUH&_r=1",
  },
  // {
  //   id: "discord",
  //   icon: IconDiscord,
  //   to: "#",
  // },
  {
    id: "instagram",
    icon: IconInstagram,
    to: "https://www.instagram.com/huncoin.co?igsh=MTMxOHRxMXh1Y3Roaw==",
  },
  // {
  //   id: "youtube",
  //   icon: IconYoutube,
  //   to: "#",
  // },
];

const Footer = () => {
  return (
    <footer className={cx("footer")}>
      <Container fluid>
        <div className="content-container">
          <div className={cx("copyright")}>&copy; 2024</div>
          <div className="social-links">
            {socialLinks.map((link) => {
              return (
                <Link to={link.to} key={link.id}>
                  <img src={link.icon} alt={link.id} width="22" />
                </Link>
              );
            })}
          </div>
        </div>
      </Container>
    </footer>
  );
};

export default Footer;
