import { Container } from "react-bootstrap";
import { Swiper, SwiperSlide } from "swiper/react";
import { Autoplay, Pagination } from "swiper/modules";
import Description from "components/Description";
import SectionTitle from "components/SectionTitle";
import RoundButton from "components/RoundButton";
import HeroImage from "assets/images/home/hero-img.png";

const roadmapSlides = [
  {
    phase: "01",
    plans: [
      "Token Launch",
      "Website Development",
      "Instagram campaign to boost community growth",
      "Reach 1000 Holders",
      "Coingecko listing",
      "CoinMarketCap Listing",
    ],
  },
  {
    phase: "02",
    plans: [
      "Coingecko listing",
      "CoinMarketCap Listing",
      "Achieve 1 Million Market Cap",
      "Ad Campaign in Tiktok",
    ],
  },
  {
    phase: "03",
    plans: [
      "Influencer Marketing",
      "Achieve 10 Million Market Cap",
      "Reach 5000 Holders",
      "Giveaway Contest",
    ],
  },
  {
    phase: "04",
    plans: [
      "Launch NFT collection",
      "Marketing/Liquidity through NFT Revenue",
      "Bigger Influencer Marketing",
      "Achieve 50 Million Market Cap",
    ],
  },
  {
    phase: "05",
    plans: [
      "CEX Listings",
      "Global promotions",
      "Achieve 1 Billion Market Cap",
      "and more...",
    ],
  },
];

const Roadmap = () => {
  return (
    <Container fluid className="roadmap-container" id="roadmap-section">
      <SectionTitle>Roadmap</SectionTitle>
      <Description>
        Embark on a journey from grassroots engagement to global dominance, as
        HUN charts a path from viral sensation to a 1 billion market cap.
        Witness our evolution through strategic expansions, innovative NFT
        launches, and community-driven milestones.
      </Description>
      <Description>We will update the roadmap as we grow.</Description>
      <Swiper
        spaceBetween={30}
        slidesPerView={1}
        centeredSlides={true}
        modules={[Autoplay, Pagination]}
        autoplay={{
          delay: 5000,
          disableOnInteraction: false,
        }}
        className="roadmap-swiper"
        pagination={{
          clickable: true,
          renderBullet: (index, className) => {
            return '<span class="' + className + '">' + (index + 1) + "</span>";
          },
        }}
      >
        {roadmapSlides.map((slide) => {
          return (
            <SwiperSlide key={slide.phase}>
              <div className="slider-content-wrapper">
                <div className="img-container">
                  <img src={HeroImage} alt="hero-img" width="100%" />
                </div>
                <div className="desc">
                  <h5>Phase {slide.phase}</h5>
                  <ul>
                    {slide.plans.map((plan, idx) => {
                      return <li key={idx}>{plan}</li>;
                    })}
                  </ul>
                  <RoundButton variant="primary">Join us</RoundButton>
                </div>
              </div>
            </SwiperSlide>
          );
        })}
      </Swiper>
    </Container>
  );
};

export default Roadmap;
