import cx from "classnames";
import "./index.scss";

const RoundButton = (props) => {
  const { children, variant, onClick, className, disabled } = props;

  return (
    <button
      className={cx("round-btn", variant, className)}
      onClick={onClick}
      disabled={disabled}
    >
      {children}
    </button>
  );
};

export default RoundButton;
