import { Col, Container, Row } from "react-bootstrap";
import { Player } from "video-react";
import SectionTitle from "components/SectionTitle";
import BuyHunImg from "assets/images/home/how-to-buy.png";
import HUNCoinVideo from "assets/images/home/HunCoin.mp4";

const Join = () => {
  return (
    <Container fluid className="join-container">
      <SectionTitle>Join the HUNCoin</SectionTitle>
      <Row>
        <Col lg={6} md={12}>
          <div className="buy-hun-img-container">
            <img src={BuyHunImg} alt="how-to-buy-hun-img" width="100%" />
          </div>
        </Col>
        <Col lg={6} md={12}>
          <div className="buy-hun-video-container">
            <Player>
              <source src={HUNCoinVideo} />
            </Player>
          </div>
        </Col>
      </Row>
    </Container>
  );
};

export default Join;
