import { Col, Container, Row } from "react-bootstrap";
import Description from "components/Description";
import SectionTitle from "components/SectionTitle";
import AboutImg from "assets/images/home/seedaj.jpg";

const About = () => {
  return (
    <Container fluid className="about-container" id="about-section">
      <SectionTitle>About us</SectionTitle>
      <Row>
        <Col lg={6} md={12}>
          <Description>
            HUN shines on Solana, with eyes set on 1 billion. Our journey from
            viral hits to a 3 500+ strong community is just the start. 'Forward
            to a billion' is our cry. Join our story.
          </Description>
        </Col>
        <Col lg={6} md={12}>
          <Description>
            HUN embodies the spirit of collaboration, making waves in the Solana
            ecosystem as the strongest meme network community.
          </Description>
        </Col>
      </Row>
      <div className="about-img-container">
        <img src={AboutImg} alt="seedaj" width="100%" />
      </div>
    </Container>
  );
};

export default About;
