import { useEffect, useState } from "react";
import { Link } from "react-router-dom";
import cx from "classnames";
import { Container, Nav, Navbar } from "react-bootstrap";
import LogoImg from "assets/images/common/logo.png";
import RoundButton from "components/RoundButton";
import "./index.scss";

const pageLinks = [
  {
    id: "home",
    text: "home",
    to: "hero-section",
  },
  {
    id: "about",
    text: "about",
    to: "about-section",
  },
  {
    id: "roadmap",
    text: "roadmap",
    to: "roadmap-section",
  },
  {
    id: "team",
    text: "team",
    to: "team-section",
  },
  {
    id: "faq",
    text: "faq",
    to: "faq-section",
  },
];

const Header = () => {
  const [isScrolledDown, setIsScrolledDown] = useState(false);
  const [prevScrollPos, setPrevScrollPos] = useState(0);

  useEffect(() => {
    const handleScroll = () => {
      const currentScrollPos = window.pageYOffset;

      setIsScrolledDown(prevScrollPos < currentScrollPos);
      setPrevScrollPos(currentScrollPos);
    };
    window.addEventListener("scroll", handleScroll);

    return () => {
      window.removeEventListener("scroll", handleScroll);
    };
  }, [prevScrollPos]);
  return (
    <header className={cx("app-header", isScrolledDown ? "static" : "sticky")}>
      <Navbar expand="lg">
        <Container fluid>
          <Navbar.Brand>
            <Link to="/">
              <div className={cx("logo-container")}>
                <img src={LogoImg} alt="logo" width="80" />
              </div>
            </Link>
          </Navbar.Brand>
          <Navbar.Toggle aria-controls="navbar-nav" />
          <Navbar.Collapse id="navbar-nav">
            <Nav className="me-auto">
              {pageLinks.map((link) => {
                return (
                  <Nav.Link
                    href={`/#${link.to}`}
                    key={link.id}
                    className="nav-link"
                  >
                    {link.text}
                  </Nav.Link>
                );
              })}
            </Nav>
            <div className="btn-wrapper">
              <a href='https://t.me/+BdmM96UBE8JmMGMy' target="_blank" class="round-btn primary a">TELEGRAM</a>
            </div>
          </Navbar.Collapse>
        </Container>
      </Navbar>
    </header>
  );
};

export default Header;
