import SectionTitle from "components/SectionTitle";
import { Col, Container, Row } from "react-bootstrap";
import Member1Img from "assets/images/home/member1.gif";
import Member2Img from "assets/images/home/member2.gif";

const Team = () => {
  return (
    <Container fluid className="team-container" id="team-section">
      <SectionTitle>Our team</SectionTitle>
      <Row>
        <Col lg={4} md={6} sm={12}>
          <div className="member-card">
            <div className="img-container">
              <img src={Member1Img} alt="founder" width="100%" />
            </div>
            <div className="info-container">
              <p className="position">Founder</p>
              <p className="name">Thomas</p>
            </div>
          </div>
        </Col>
        <Col lg={4} md={6} sm={12}>
          <div className="member-card">
            <div className="img-container">
              <img src={Member2Img} alt="founder" width="100%" />
            </div>
            <div className="info-container">
              <p className="position">Founder</p>
              <p className="name">Theo</p>
            </div>
          </div>
        </Col>
      </Row>
    </Container>
  );
};

export default Team;
